import React from 'react';
import Container from '@material-ui/core/Container';

import TitleBar from '../TitleBar/TitleBar';
import Flag from 'react-world-flags'
import Countries from 'world-countries';

import './Main.css';


function Main() {
  return (
    <Container maxWidth="md">
      <TitleBar/>
      <p>July 4, 2020</p>
      <p>We understand that today is the 244th recognition of the independence of the United States of America</p>
      <p>But despite the gross, unwashed calls for nationalism in the United States of America on this day, we're calling for peace, compassion, and true freedom for ALL people of the world</p>
      <p>We understand that the United States of America is currently the epicenter of a worldwide pandemic with no so sign of decelerating any time soon</p>
      <p>But in the name of TRUE equality, it would be inappropriate to focus on just ONE country when ALL COUNTRIES need our support and love</p>
      <p>So please spend some time thinking about, and learning about any onther country BESIDES The United States of America</p>
      <p>Because at the end of the day, despite what's currently going on in the United States of America, we should never loose sight of the fact that...</p>
      <p><a href='https://twitter.com/search?q=%23AllCountriesMatter'>ALL COUNTRIES MATTER</a></p>
      <div className='world-box'>
      {Countries.map(country => {
        if (country.cca2 !== 'US' && country.cca2 !== 'UM' && country.cca2 !== 'AQ') {
          return (
            <div className='independent-box' key={country.cca2}>
              <Flag code={country.cca2} height='64'/>
              <br/>
              <span className='country-name'>{country.name.official}</span>
              <br/>
              Capital: {country.capital}
              <br/>
              <a href='https://en.wikipedia.org/wiki/Demonym'>Demonym:</a> {country.demonyms.eng.f}
              <br/>
              <a href='https://en.wikipedia.org/wiki/Top-level_domain'>TLD:</a> {country.tld[0]}
              <br/>
              <br/>
              <br/>
            </div>
          )
        }
        return null;
      })}
      
      
      
      
      </div>
    </Container>
  );
}

export default Main;
